#header{
    width: 100%;
    height: 100vh;
    background-image: url(backgroundd.png);
    background-size: cover;
    background-position: center;
}

.container{
    padding: 10px 10%;
}

nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

nav ul li {
    display: inline-block;
    list-style: none;
    margin: 10px 20px;
}

nav ul li a {
    color: #fff;
    text-decoration: none;
    font-size: 18px;
    position: relative;
}

nav ul li a::after{
    content: '';
    width: 0;
    height: 3px;
    background: #03346E;
    position: absolute;
    left: 0;
    bottom: -6px;
    transition: 0.5s;
}

nav ul li a:hover::after{
    width: 100%;

}


.title{
    color: #fff;
    text-decoration: underline;
    font-size: 50px;
}

.header-text{
    margin-top: 5%;
    font-size: 40px;
}

.header-text h1{
    font-size: 40px;
    margin-top: 15px;
}

.header-text h1 span {
    color: red;
    font-size: 50px;
}